<template>
    <v-container>
        <v-row class="ma-0 pa-0">
            <!--왼쪽 메뉴-->
            <v-col cols="auto" id="leftMenu" style="display:block;margin-top: 0px;">    
                <template class="ma-0 pa-0">
                    <v-card class="mx-auto" width="300">
                        <v-list>
                            <v-list-group v-bind:value="leftMenuGroup.seller" prepend-icon="mdi-account-circle">
                                <template v-slot:activator>
                                    <v-list-item-title align="start">Order</v-list-item-title>
                                </template>
                                <v-list-item id="report-weight-list" @click="leftMenuClick('report-weight-list')">
                                    <v-list-item-title style="margin-left:60px;" align="start">Actual weight</v-list-item-title>
                                </v-list-item>
                            </v-list-group>
                        </v-list>
                        <v-list-item link @click="leftMenu('hide')">
                            <v-list-item-icon><v-icon>mdi-swap-horizontal</v-icon></v-list-item-icon>
                            <v-list-item-title align="start">Hidden</v-list-item-title>
                        </v-list-item>
                    </v-card>
                </template>
            </v-col>
            <!--왼쪽 메뉴-->
            <div><v-list-item-icon @click="leftMenu('show')" style="cursor:pointer;display:none" id="leftMenuHide"><v-icon>mdi-swap-horizontal</v-icon></v-list-item-icon></div>  
            <v-col>
                <!--검색-->
                <v-row>
                    <v-col>
                        <v-row>
                            <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                                &nbsp;<vue-country-code style="display:none" />
                            </v-col>
                            <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                                <v-menu v-model="fromDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-text-field v-model="fromDate" label="From" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="fromDate" @input="fromDateMenu = false"></v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                                <v-menu v-model="toDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-text-field v-model="toDate" label="To" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="toDate" @input="toDateMenu = false"></v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col align="left" class="ma-0 pa-0 flex-grow-0 flex-shrink-0">
                                <v-tooltip top color="#18A2D9">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:10px" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/magnify.png" width="40px" @click="getDataInit()" />
                                    </template>
                                    <span>Search</span>
                                </v-tooltip>
                            </v-col>
                        </v-row>
                    </v-col>
                    <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <v-tooltip top color="#18A2D9">
                            <template v-slot:activator="{ on, attrs }">
                                <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px;margin-right:30px;" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/download-file_toolbar.png" width="40px" @click="getMakeExcel()"/>
                            </template>
                            <span>Download</span>
                        </v-tooltip>
                    </v-col>
                </v-row>
                <!--검색-->
                <v-row>
                    <v-col align="left" style="padding-top:10px;padding-bottom:0px;">
                        &nbsp;&nbsp;&nbsp;{{ selected.length }} / {{ desserts.length }} 
                    </v-col>
                </v-row>
                <!-- 리스트 -->
                <v-row>
                    <v-col>
                        <v-data-table :headers="headers" :items="desserts" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
                            <template v-slot:item="props">
                                <tr style="height:40px;">
                                    <td style="height:30px;" align="center">
                                        <input type="checkbox" style="width:20px;height:20px" @change="setCheck(props.item, $event.target.checked)" name="item">
                                    </td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="start">{{ props.item.shipDate }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="start">{{ props.item.gemMasterNo }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="start">{{ props.item.orderNo }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.recipientCountry }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ getCarrierName(props.item.carrierCode) }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.orderGW }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.orderVW }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualGW }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualVW }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualWidth }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualLength }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualHeight }}</td>
                                    <td style="height:30px;cursor:pointer" @click="getWeightDetail(props.item)" align="center">{{ props.item.actualCW }}</td>
                                </tr>
                            </template>
                        </v-data-table>
                    </v-col>
                </v-row>
                <!-- 리스트 -->
            </v-col>
        </v-row>
        <!-- 컴퍼넌트화-->
        <!-- weightDetail 다이얼로그 -->
        <orderDetailView :setOrderDetail="this.setOrderDetail" @close="closeOrderDetailViewDialog"></orderDetailView>             
        <!-- weightDetail 다이얼로그 -->
        <!-- 로딩바 -->
        <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
        <!-- 로딩바 -->  
        </v-container>
</template>

<style scoped>
.bg-active {
  background-color: #0aaae9;
  color : white !important;
}
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #9BC298;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 2;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.headerCss {
  background-color: darksalmon;
  font-size: 20px;
  background-color: red;
}
.v-text-field {
  width: 250px;
}

.v-data-table > .v-data-table__wrapper > table > tbody > tr > td,
.v-data-table > .v-data-table__wrapper > table > tfoot > tr > td,
.v-data-table > .v-data-table__wrapper > table > thead > tr > td {
  font-size: 12px;
}
</style>
<script>
import VueCookies from "vue-cookies";
import $ from "jquery";
import axios from "axios";
import common from '@/common'
import dateUtil from "@/common/DateUtil"
import loadingBarView from "@/components/LoadingBarView.vue"
import orderDetailView from "@/components/OrderDetailView.vue"

export default {
    components: {
        orderDetailView,
        loadingBarView,
    },
    data() {
        return {
            loginId: this.$session.get('loginId'),
            setOrderDetail: {
                show: false,
                orderDetail: "",
                deleteBtnShow: false,
                modifyBtnShow: false,
            },
            weightDetailDialogShow: false,
            setLoadingBar : { loadingBarDialogShow : false },
            leftMenuGroup : { seller : true, order : false},
            fromDateMenu: false,
            toDateMenu: false,
            fromDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
            toDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
            searchText: "",
            weightDetail: "",
            selectedTasks: [],
            selected: [],
            desserts: [],
            headers: [
                { text: "Ship date", value: "shipDate", align: "left", width:"102px" },
                { text: "GEM Master No", value: "gemMasterNo", align: "center" },
                { text: "Order No", value: "orderNo", width:"140px" },
                { text: "Country", value: "recipientCountry", align: "center", width:"96px"},
                { text: "Carrier", value: "carrierCode", align: "center" },
                { text: "Order GW", value: "orderGW", align: "center" },
                { text: "Order VW", value: "orderVW", align: "center" },
                { text: "Actual GW", value: "actualGW", align: "center", class: "actual_1" },
                { text: "Actual VW", value: "actualVW", align: "center", class: "actual_1" },
                { text: "Actual Width", value: "actualWidth", align: "center", class: "actual_1" },
                { text: "Actual Length", value: "actualLenght", align: "center", class: "actual_1" },
                { text: "Actual Height", value: "actualHeight", align: "center", class: "actual_1" },
                { text: "CW", value: "actualCW", align: "center", class: "actual_2" },
            ],
        }
    },
    filters: {
        toFixed: function(val, num) {
            return parseFloat(val).toFixed(num);
        }
    },
    computed: {
    },
    methods: {
        getDataInit() {
            document.querySelector(".v-data-table__wrapper").scrollTo({ left: 0, top: 0 });
            this.setLoadingBar.loadingBarDialogShow = true;
            axios
            .get(common.makeAPIUrl('/sellers/' + this.loginId + '/reports/order/actual-weight?startDate=' + this.fromDate + '&endDate=' + this.toDate)
            , { headers: {'Authorization': this.$session.get('authorization')}})
            .then(response => {
                this.desserts = response.data;
                for(var weightCount = 0; weightCount < this.desserts.length; weightCount++)
                {
                    this.desserts[weightCount].shipDate = common.getYYYMMDD(this.desserts[weightCount].shipDate)
                }
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert(common.messages.login, "", "info").then(() => { this.$router.push("/"); });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        leftMenu(display) {
            $("#leftMenu").hide();
            $("#leftMenuHide").hide();

            if(display == "show") { $("#leftMenu").show(); }
            if(display == "hide") { $("#leftMenuHide").show(); }
        }, 
        getWeightDetail(item) {
            this.setLoadingBar.loadingBarDialogShow = true;
            axios
            .get(common.makeAPIUrl("/sellers/" + this.loginId + "/orders/" + item.orderId), {
            headers: { "Content-Type": "application/json", Authorization:  this.$session.get("authorization") },
            })
            .then((response) => {
                if (response.status == 200) {
                    this.setOrderDetail.orderDetail = response.data;
                    this.orderDetail = response.data;
                    this.setOrderDetail.show = true;
                    this.setOrderDetail.deleteBtnShow = false;
                    this.setOrderDetail.modifyBtnShow = false;
                    this.setLoadingBar.loadingBarDialogShow = false;
            }
            })
            .catch((err) => {
            this.$alert(err.response.data.message, "", "info").then(() => {});
            });
        },     
        closeOrderDetailViewDialog () {
            this.setOrderDetail.show = false
        },   
        setAllCheck(e) {
            var checked = e.target.checked;
            this.selected = [];
            $('input[name="item"]').prop('checked', checked)
                if(checked) {
                    for(var count=0; count < this.desserts.length; count++) {
                        this.selected.push(this.desserts[count]);
                    }
                }
        },
        setCheck(item, checked) {
            if(checked) {
                this.selected.push(item);
            } else {
                const idx = this.selected.indexOf(item)
                if (idx > -1) { this.selected.splice(idx, 1) }
            }
        },        
        keyEnter(value) {
            if (value.code == "Enter") {
                this.getDataInit();
            }
        },
        getMakeExcel() {
            this.selectedTasks = this.selected;
            var vm = this;
            if(this.selectedTasks != "") {
                vm.setLoadingBar.loadingBarDialogShow = true;
                let parmas = [];
                for(let weightCount = 0; weightCount < this.selectedTasks.length; weightCount++) {
                    parmas.push(this.selectedTasks[weightCount].orderId);
                }
                axios
                .get(common.makeAPIUrl("/sellers/"+ this.loginId + "/reports/order/actual-weight/download?orderIds=" + parmas), {
                    headers: { "Content-Type": "application/json", Authorization: this.$session.get("authorization") },
                })
                .then((response) => {
                    var excelDataList = [];

                    excelDataList.push(["Ship Date", "Gem Master No", "Order No", "Recipient Country", "Carrier", "Order GW", "Order VW", "Actual GW", "Actual VW", "Actual Width", "Actual Length", "Actual Height", "CW"]);
                    for(var weightCount=0;weightCount<response.data.length;weightCount++) {
                        var ShipDate = common.getYYYMMDD(response.data[weightCount].shipDate);
                        var GemMasterNo = response.data[weightCount].gemMasterNo;
                        var OrderNo = response.data[weightCount].orderNo;
                        var RecipientCountry = response.data[weightCount].recipientCountry;
                        var Carrier = this.getCarrierName(response.data[weightCount].carrierCode);
                        var OrderGW = response.data[weightCount].orderGW;
                        var OrderVW = response.data[weightCount].orderVW;
                        var ActualGW = response.data[weightCount].actualGW;
                        var ActualVW = response.data[weightCount].actualVW;
                        var ActualWidth = response.data[weightCount].actualWidth;
                        var ActualLength = response.data[weightCount].actualLength;
                        var ActualHeight = response.data[weightCount].actualHeight;
                        var ActualCW = response.data[weightCount].actualCW;
                        
                        
                        excelDataList.push([
                        ShipDate, GemMasterNo, OrderNo, RecipientCountry, Carrier, OrderGW, OrderVW, ActualGW, ActualVW, ActualWidth, ActualLength, ActualHeight, ActualCW
                        ]);      
                    }

                    const xlsx = require('xlsx');
                    const workBook = xlsx.utils.book_new()
                    const workSheet = xlsx.utils.aoa_to_sheet(excelDataList)
                    // 컬럼크기에 맞게 자동 넓이 지정 시작
                        let objectMaxLength = []
                        excelDataList.map(arr => {
                        Object.keys(arr).map(key => {
                            let value = arr[key] === null ? '' : arr[key]
                            if (typeof value === 'number') { return objectMaxLength[key] = 10 }
                            objectMaxLength[key] = objectMaxLength[key] >= value.length ? objectMaxLength[key]  : value.length
                            })
                        })
                        let worksheetCols = objectMaxLength.map(width => {
                            return { width }
                        })
                        workSheet["!cols"] = worksheetCols
                    // 컬럼크기에 맞게 자동 넓이 지정 끝
                    xlsx.utils.book_append_sheet(workBook, workSheet, 'Order_ActualWeight')

                    xlsx.writeFile(workBook, 'GEM_' +  VueCookies.get("companyName") + '_ActualWeight' + "_" + dateUtil.nowYYYYmmdd() + ".xlsx")
                    
                    vm.setLoadingBar.loadingBarDialogShow = false;
                })
                .catch((err) => {
                    vm.setLoadingBar.loadingBarDialogShow = false;
                    vm.$alert(err.response.data.message, "", "info").then(() => {});
                });
            } else {
                this.$alert(common.messages.weightSelect, '', 'info').then(() => { vm.setLoadingBar.loadingBarDialogShow = false; })   
            }
        },
        getCarrierName(value) {
          return common.getCarrierName(value);
        },
    },
    mounted() {
        if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
            this.$alert(common.messages.login, "", "info").then(() => {
                this.$router.push("/");
            });
            return false;
        }
              
        $(document).prop("title", "Report Management");
        $("#bar_header").attr("style", "background-color:#E2E1F1");
        $("#top_header").show();
        $("#topNaviText").text("Report Management");

        // this.fromDate = dateUtil.fromYYYYmmdd();
        // this.toDate = dateUtil.toYYYYmmdd();

        var dataTableHeight = "height:" + String(window.innerHeight - 280) + "px";

        $("#dataTableContent").children().attr("style", dataTableHeight);
        $(".v-data-table-header>").prepend("<th style='width:15px'><input type='checkbox' style='width:20px;height:20px' id='allCheck' name='allCheck'></th>")

        this.targetAllCheck = document.querySelector('#allCheck');
        this.targetAllCheck.addEventListener('change', this.setAllCheck);

        this.getDataInit();
        
        $("#tabBar>div>div>div>div").next().css({'background-color':'#6587CD' });
        $("#tabBar>div>div>div>div").next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").css({'margin-right':'8px' });
        $("#tabBar>div>div>div>div").css({'color':'#FFFFFF' });

        let UrlSplit = window.location.href.split("/");
        let leftPosition = UrlSplit[UrlSplit.length-1];
        $("#" + leftPosition).addClass("bg-active");
        
        $(".v-data-table-header>tr>th").css({'background-color':'#1BB5F2' });
        $(".v-data-table-header>tr>th.actual_1").css({'background-color':'#FFC8A2' });
        $(".v-data-table-header>tr>th.actual_2").css({'background-color':'#FF968A' });
        $(".v-data-table-header>tr>th").css({'text-align':'center' });
        $(".v-data-table-header>tr>th").css({'font-size':'12px' });
        $(".v-data-table-header>tr>th").css({'font-style':'bold' });
        $(".v-data-table-header>tr>th").css({'margin-right':'1px' ,'margin-left':'1px' });

    },     
}
</script>